.modalName {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: hsla(0, 0%, 50%, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.modalName__content {
  padding: 0px 15px 0px 15px;
  border: 1px solid black;
  border-radius: 20px;
  width: 100%;
  height: 100%;
  max-width: 450px;
  max-height: 300px;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
.modalName__content__title {
  font-size: 2em;
  font-weight: bold;
  color: rgb(41, 41, 41);
}
.modalName__content__input {
  font-size: 1.2em;
  border-radius: 10px;
}
.modalName__content__button {
  max-width: 150px;
  padding: 5px;
}/*# sourceMappingURL=modalName.css.map */