.button {
  font-size: 1.1em;
  background-color: black;
  color: white;
  border-radius: 10px;
  padding: 10px 0 10px 0;
  width: 100%;
}

button:hover {
  background-color: rgb(49, 49, 49);
  cursor: pointer;
}

button:active {
  background-color: rgb(158, 158, 158);
  color: black;
}/*# sourceMappingURL=button.css.map */